<template>
	<div>
		<header-nav></header-nav>
		<person-info></person-info>
		<p class="word-a">左右滑动</p>
		<p class="word-b">按钮点击</p>
		<footer-nav :class='{"isHomePage":isNowPage}'></footer-nav>
		<touch-it></touch-it>
	</div>
</template>

<script>
	import PersonInfo from '@/components/PersonInfo'
	import FooterNav from '@/components/Footer.vue'
	import HeaderNav from '@/components/Header.vue'
	import touchIt from '@/components/touch.vue'
	export default{
		components:{
			PersonInfo,
			FooterNav,
			HeaderNav,
			touchIt
		},
        data(){
        	return {
        		isNowPage:true
        	}
        }
	}
</script>

<style>
	.word-a{
		position: absolute;
		top: 200px;
		width: 100%;
	}
	.word-b{
		position: absolute;
		top: 220px;
		width: 100%;
	}
</style>


