<template>
	<div>
		<table-to-case></table-to-case>
		<table style="margin-top: 80px;">
			<thead>
				<tr>
					<th>名称</th>
					<th>价钱</th>
					<th>数量</th>
					<th>操作</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(good,index) in goods">
					<td><input style="width: 60px;text-align: center;" type="text" v-model="good.name"></td>
					<td><input style="width: 30px;text-align: center;" type="text" v-model="good.price"></td>
					<td><button @click="numReduce(index)" style="padding: 0 4px;" class="reduce">-</button><input style="width: 20px;text-align: center;" type="text" v-model="good.count"><button @click="numPlus(index)" style="padding: 0 3px;" class="plus">+</button></td>
					<td><button @click="deleteGood(index)">删除</button></td>
				</tr>
			</tbody>
		</table>
		<div><button @click="addNewGood">增加商品 </button> 总价：{{getTotal.totalPrice}}</div>
		<footer-nav :class='{"isBuyCase":isNowPage}'></footer-nav>
	</div>
</template>

<style scoped>
	table{
		margin: 0 auto;
	}
</style>

<script>
	import FooterNav from '@/components/Footer'
	import TableToCase from '@/components/tableToCase'
	export default{
		components:{
			FooterNav,
			TableToCase
		},
		data(){
			return{
				goods:[{
					name:'苹果',
					price:5,
					count:1
				},
				{
					name:'香蕉',
					price:2,
					count:1
				}],
				isNowPage:true,
				newGood:{name:'',price:0,count:0}
			}
		},
		methods:{
			numReduce(index){
				if (this.goods[index].count<1) return;
				this.goods[index].count--;
			},
			numPlus(index){
				this.goods[index].count++;
			},
			deleteGood(index){
				this.goods.splice(index,1);
			},
			addNewGood(){
				this.goods.push(this.newGood)
			}
		},
		computed:{
			getTotal(){
				var totalPrice=0;
				for (var i = 0; i < this.goods.length; i++) {
					totalPrice+=this.goods[i].price*this.goods[i].count;
				}
				return {totalPrice,totalPrice}
			}
		}
	}
</script>


