<template>
	<div>
		<router-link to="/picGallery" class="returnPic">返回</router-link>
		<v-touch @swipeleft="right" @swiperight="left">
			<div style="width: 100%;height: 100%;">

				<transition-group tag="ul" :name="slide" style="position: absolute;width: 100%;height: 100%;background-color: #333">
					<li class="img-list" v-for="(item,index) in warList" :key="index"  v-show="index===mark">
						<img :src="item.picCode" alt="">
					</li>
				</transition-group>
			</div>
		</v-touch>
	</div>
</template>

<script>
	export default{
		components:{
			
		},
		data(){
			return{
				isClicked:false,
				warList:[{picCode:'./static/assets/d1.jpg'},{picCode:'./static/assets/d2.jpg'},{picCode:'./static/assets/d3.jpg'},{picCode:'./static/assets/d4.jpg'}],
				mark:0,
				slide:'slide-right'
			}
		},
		methods:{
			showPic(){
				this.isClicked=!this.isClicked
			},
			right(){
				this.slide='slide-right'
				this.mark<4?this.mark++ : this.mark=0
			},
			left(){
				this.slide='slide-left'
				this.mark>0?this.mark-- : this.mark=4
			}
		}
	}
</script>

<style scoped>
    .img-list{
    	position: absolute;
    	height: 100%;
    	display: flex;
    	justify-content:center;
    	align-items:center;
    }
    .slide-right-enter{
    	transform: translateX(100%);
    	/*opacity: 0;*/
    }
    .slide-right-enter-to{
    	transition: transform 0.5s;
    }
    .slide-right-leave-to{
    	transform: translateX(-100%);
    	transition: transform 0.5s;
    	/*opacity: 0;*/
    }

    .slide-left-enter{
    	transform: translateX(-100%);
    	/*opacity: 0;*/
    }
    .slide-left-enter-to{
    	transition: transform 0.5s;
    }
    .slide-left-leave-to{
    	transform: translateX(100%);
    	transition: transform 0.5s;
    	/*opacity: 0;*/
    }
    /*.pic-slide-leave{
    	transform: translateX(0);
    }*/

	img{
		width: 100%;
		vertical-align: top;
	}
	.returnPic{
		position: absolute;
		left: 0;
		top: 0;
		display: block;
		width: 60px;
		height: 40px;
		text-align: center;
		line-height: 40px;
		background-color: #ccc;
		z-index: 10;
		border-radius: 4px;
		border-top-left-radius: 0px;
	}
</style>


