<template>
	<div>
		<div class="describe">购物车界面</div>
		<v-touch v-on:swiperight='left' v-on:swipeleft='right' class='jump-router'></v-touch>
	</div>
</template>

<script>
	export default({
        methods:{
            left(){
                // this.$store.commit('showMenu')
                this.$router.push('/doTable')
            },
            right(){
            	this.$router.push('/picGallery')
            }
        }
    })
</script>

<style scoped>
	.describe{
		position: fixed;
		top: 0;
		background-color: #ccc;
		width: 100%;
		line-height: 40px;
	}
	.jump-router{
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}
</style>


