<template>
	<div>
		<jump-home></jump-home>
		<fieldset>
			<legend>表格</legend>
			<div>
				<label>姓名</label>
				<input type="text" v-model="newPerson.name">
			</div>
			<div>
				<label>年龄</label>
				<input type="text" v-model="newPerson.age">
			</div>
			<div>
				<label>性别</label>
				<select v-model="newPerson.sex">
					<option value="男">男</option>
					<option value="女">女</option>
				</select>
				<button @click="add">添加</button>
			</div>
		</fieldset>
		<table>
			<thead>
				<tr>
					<th>姓名</th>
					<th>年龄</th>
					<th>性别</th>
					<th>操作</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(person,index) in people">
					<td>{{person.name}}</td>
					<td>{{person.age}}</td>
					<td>{{person.sex}}</td>
					<td><button @click="editPerson(index)">修改</button><button @click="deletePerson(index)">删除</button></td>
				</tr>
			</tbody>
		</table>
		<div class="mask" v-show="showEdit">
			<div class="mask-in">
				<div>
					<label>姓名</label>
					<input type="text" v-model="newEditPerson.name">
				</div>
				<div>
					<label>年龄</label>
					<input type="text" v-model="newEditPerson.age">
				</div>
				<div>
					<label>性别</label>
					<select v-model="newEditPerson.sex">
						<option value="男">男</option>
						<option value="女">女</option>
					</select>
					<button @click="edit">修改</button>
					<button @click="cancel">取消</button>
				</div>
			</div>
		</div>
		<footer-nav :class="{'isDoTablePage':isNowPage}"></footer-nav>
	</div>
</template>

<script>
	import FooterNav from '@/components/Footer'
	import JumpHome from '@/components/jumpHome'
	export default{
		components:{
			FooterNav,
			JumpHome
		},
        data(){
        	return {
        		isNowPage:true,
        		showEdit:false,
        		newPerson:{
        			name:'',
        			age:'',
        			sex:'男'
        		},
        		people:[
        		{
        			name:'小猫',
        			age:3,
        			sex:'女'
        		},{
        			name:'小狗',
        			age:5,
        			sex:'男'
        		},{
        			name:'小虾',
        			age:4,
        			sex:'男'
        		}],
        		indexNum:'',
        		newEditPerson:{
        			name:'',
        			age:'',
        			sex:'男'
        		}
        	}
        },
        methods:{
        	add(){
        		var vn=this.newPerson.name
        		var va=this.newPerson.age
        		if (vn.trim()=='') {
        			alert('请输入姓名')
        		}else if(va.trim()==''){
        			alert('请输入年龄')
        		}else if(typeof(va.trim())==='string'){
        			alert('请输入数字')
        		}else{
        			this.people.push(this.newPerson)
        			this.newPerson={name:'',age:'',sex:''}
        		}
        	},
        	deletePerson(index){
        		this.people.splice(index,1)
        	},
        	editPerson(index){
        		this.indexNum=index
        		this.showEdit=true
        	},
        	edit(){
        		var vn=this.newEditPerson.name
        		var va=this.newEditPerson.age
        		var vs=this.newEditPerson.sex
        		if (vn.trim()=='') {
        			alert('请输入姓名')
        		}else if(va.trim()==''){
        			alert('请输入年龄')
        		}else if(typeof(va.trim())==='string'){
        			alert('请输入数字')
        		}else{
        			this.people.splice(this.indexNum,1,this.newEditPerson)
        			this.showEdit=false
        			this.newEditPerson={name:'',age:'',sex:''}
        		}
        	},
        	cancel(){
        		this.showEdit=false
        	}
        }
	}
</script>

<style>
	fieldset{
		display: inline-block;
		padding: 10px 10px 3px 10px;
		margin-top: 50px;
	}
	fieldset div:last-child{
		text-align: left;
	}
	fieldset div:last-child button{
		display: block;
		position: relative;
		margin-top: 3px;
		left: 37px;
	}
	table{
		margin: 0 auto;
		padding: 0 6px;
		border-width: 2px;
		border-style: groove;
		border-color: threedface;
		border-image: initial;
	}
	table thead th{
		padding: 0 10px;
	}
	.mask{
		display: flex;
		justify-content:center;
		align-items:center;
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,.5);
	}
	.mask-in{
		background-color: #ccc;
		padding: 5px;
	}
	
</style>


