<template>
	<div class="personal">
		<transition name="side">
			<div class="personal-side" v-show="isMenuShow"></div>
		</transition>
		<transition name="fade">
			<div class="personal-fade" v-show="isMenuShow" @click="hideMenu"></div>
		</transition>
	</div>
</template>
<script>
    export default({
        computed:{
            isMenuShow(){
                return this.$store.state.personal.isMenuShow
            }
        },
        methods:{
            hideMenu(){
                this.$store.commit('hideMenu')
            }
        }
    })
</script>
<style scoped>
	.personal{
		width: 100%;
		height: 100%;
	}
	.personal-side{
		position: fixed;
		left: 0;
		top: 0;
		width: 50%;
		height: 100%;
		background-color: #333;
		z-index: 1001;
	}
	.personal-fade{
		position: fixed;
		right: 0;
		top: 0;
		width: 50%;
		height: 100%;
	}
	.side-enter-to, .side-leave-to{
        transition: transform 0.5s
    }	
	.side-enter, .side-leave-to{
        transform:translateX(-100%)
    }
	.fade-enter-to, .fade-leave-to{
        transition: opacity 0.3s
    }	
	.fade-enter, .fade-leave-to{
        opacity: 0	
    }
</style>


