<template>
	<div class="top-nav">
		<div class="describe"><button @click="showMenu">个人中心</button>首页</div>
		<v-touch v-on:swipeleft='right' @tap='hideMenu' @swiperight='showMenu' class='jump-router'></v-touch>
	</div>
</template>

<script>
	export default({
        computed:{
            isMenuShow(){
                return this.$store.state.personal.isMenuShow
            }
        },
        methods:{
        	showMenu(){
        		this.$store.commit('showMenu')
        	},
        	right(){
                // this.$store.commit('showMenu')
                if (this.$store.state.personal.isMenuShow) {
                	this.$store.commit('hideMenu')
                }else{
                	this.$router.push('/doTable')
                }
            },
            hideMenu(){
            	if (this.$store.state.personal.isMenuShow) {
                	this.$store.commit('hideMenu')
                }
            }
        }
    })
</script>

<style scoped>
	/*.personMenu{
		display: inline-block;
		position: absolute;
		left: 0;
		top: 10px;
		border: 1px solid #333;
		border-radius: 3px;
	}*/
	.top-nav{
		height: 40px;
		position: relative;
		z-index: 100;
	}
	button{
		position: absolute;
		left: 0;
		top: 10px;
		border-radius: 3px;
		z-index: 100;
	}
	.jump-router{
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -100;
	}
	.describe{
		position: fixed;
		top: 0;
		background-color: #ccc;
		width: 100%;
		line-height: 40px;
	}
</style>


