<template>
	<div class="picBg">
		<back-to-case></back-to-case>
		<div class="a">
			<div class="pic-box">
				<router-link to="/picGallery/war"><img src="../assets/6.jpg" alt=""></router-link>
				<p>战场(共5张)</p>
			</div>
			<div class="pic-box">
				<router-link to="/picGallery/hero"><img src="../assets/8.jpg" alt=""></router-link>
				<p>人物(共5张)</p>
			</div>
			<div class="pic-box" style="margin-bottom: 40px;">
				<router-link to="/picGallery/messenger"><img src="../assets/d1.jpg" alt=""></router-link>
				<p>信使(共4张)</p>
			</div>
		</div>
		<footer-nav :class='{"isPicGallery":isNowPage,"isSeen":isClicked}'></footer-nav>
	</div>
</template>

<script>
	import FooterNav from '@/components/Footer'
	import backToCase from '@/components/backToCase'
	export default{
		components:{
			FooterNav,
			backToCase
		},
		data(){
			return{
				isNowPage:true,
				isClicked:false,
				warList:[{picCode:'./static/assets/6.jpg'},{picCode:'./static/assets/8.jpg'},{picCode:'./static/assets/11.jpg'},{picCode:'./static/assets/12.jpg'}],
				mark:0
			}
		},
		methods:{
			showPic(){
				this.isClicked=!this.isClicked
			},
			right(){
				this.mark<3?this.mark++ : this.mark=0
			}
		}
	}
</script>

<style scoped>
p{
	position: absolute;
	right: 0;
	bottom: 0;
	background-color: #f2f2f2;
}
.picBg{
	background-color: #333;
}
.a{
	width: 75%;
	margin: 0 auto;
}
    .pic-slide-enter{
    	transform: translateX(100%);
    	/*opacity: 0;*/
    }
    .pic-slide-enter-to{
    	transition: transform 0.5s;
    }
    .pic-slide-leave-to{
    	transform: translateX(-100%);
    	transition: transform 0.5s;
    	/*opacity: 0;*/
    }

    /*.pic-slide-leave{
    	transform: translateX(0);
    }*/
	.pic-box{
		position: relative;
		height: 100%;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: center;
		-ms-align-items: center;
		align-items: center;
		background-color: #333;
		margin-top: 40px;
		border: 2px solid #666;
		border-radius: 4px;
	}
	img{
		width: 100%;
		vertical-align: top;
	}
</style>


