<template>
	<div class="head">
		<button @click="showMenu" class='personMenu'>用户列表</button>
	</div>
</template>

<script>
	export default({
        computed:{
            isMenuShow(){
                return this.$store.state.personal.isMenuShow
            }
        },
        methods:{
            showMenu(){
                this.$store.commit('showMenu')
            },
            right(){
                this.$store.commit('showMenu')
            }
        }
    })
</script>

<style scoped>
	.head{
		position: fixed;
		top: 0;
		line-height: 40px;
		background-color: #ccc;
		width: 100%;
		position: relative;
	}
	.personMenu{
		display: inline-block;
		position: absolute;
		left: 0;
		top: 10px;
	}
</style>


