<template>
  <div id="app">
    <transition name="fade">
      <router-view class='child-view'></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'App'
  }
</script>

<style>

*{
  margin: 0;
  padding: 0;
  list-style: none;
}
a{
  text-decoration: none;
  color: #000;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.child-view{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
}

.fade-enter{
  opacity: 0;
}
.fade-enter-to{
  transition: opacity .5s;
}
.fade-leave-to{
  transition: opacity .5s;
  opacity: 0;
}


/*.slide-left-enter{
  transform: translateX(-100%);
}
.slide-left-enter-to{
  transition: transform .5s;
}
.slide-left-leave-to{
  transition: transform .5s;
  transform: translateX(100%);
}*/
</style>



