<template>
	<div>
		<div class="describe">图片库界面</div>
		<v-touch v-on:swiperight='left' class='jump-router' style="background-color: #333"></v-touch>
	</div>
</template>

<script>
	export default({
        methods:{
            left(){
                // this.$store.commit('showMenu')
                this.$router.push('/buyCase')
            }
        }
    })
</script>

<style scoped>
	.describe{
		position: fixed;
		top: 0;
		background-color: #ccc;
		width: 100%;
		line-height: 40px;
		z-index: 1;
	}
	.jump-router{
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}
	.isSeen{
		display: none;
	}
</style>


