import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import doTable from '@/views/doTable'
import Page from '@/views/page'
import buyCase from '@/views/buyCase'
import picGallery from '@/views/picGallery'
import war from '@/views/war'
import hero from '@/views/hero'
import messenger from '@/views/messenger'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'Page',
      component:Page
    },
    {
      path:'/doTable',
      name:'doTable',
      component:doTable
    },
    {
      path:'/buyCase',
      name:'buyCase',
      component:buyCase
    },
    {
      path:'/picGallery',
      name:'picGallery',
      component:picGallery
    },
    {
      path:'/picGallery/war',
      name:'war',
      component:war
    },
    {
      path:'/picGallery/hero',
      name:'hero',
      component:hero
    },
    {
      path:'/picGallery/messenger',
      name:'messenger',
      component:messenger
    }
  ]
})



